:root {
  --primary: #0a192f;
  --primary-light: #112240;
  --primary-lighter: #233554;
  --secondary: #00d4aa;
  --secondary-dark: #00b894;
  --accent: #0095ff;
  --text: #e6f1ff;
  --text-light: #a8b2d1;
  --text-lighter: #ccd6f6;
  --white: #ffffff;
  --gradient: linear-gradient(135deg, #00d4aa 0%, #0095ff 100%);
  --shadow: 0 10px 30px -10px rgba(2, 12, 27, 0.7);
  --shadow-lg: 0 20px 40px -10px rgba(2, 12, 27, 0.8);
  --transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  --border-radius: 12px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--primary);
  color: var(--text-light);
  line-height: 1.6;
  overflow-x: hidden;
  font-weight: 400;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Header & Navigation */
header {
  background: rgba(10, 25, 47, 0.9);
  backdrop-filter: blur(5px);
  position: fixed;
  width: 100%;
  z-index: 1000;
  transition: var(--transition);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 0;
  height: 80px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 15px;
}

.logo-image {
  width: 50px;
  height: 50px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background: var(--primary-light);
  border: 1px solid rgba(100, 255, 218, 0.1);
}

.logo-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  padding: 5px;
}

.logo-text {
  display: flex;
  flex-direction: column;
}

.lab-name {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text);
  line-height: 1.2;
}

.university-name {
  font-size: 0.85rem;
  color: var(--text-light);
  font-weight: 500;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 35px;
  align-items: center;
}

.nav-links a {
  text-decoration: none;
  color: var(--text-light);
  font-weight: 500;
  transition: var(--transition);
  position: relative;
  font-size: 0.95rem;
}

.nav-links a:hover,
.nav-links a.active {
  color: var(--secondary);
}

.nav-links a::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--gradient);
  transition: var(--transition);
  border-radius: 2px;
}

.nav-links a:hover::after,
.nav-links a.active::after {
  width: 100%;
}

.github-link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: rgba(100, 255, 218, 0.1);
  border: 1px solid rgba(100, 255, 218, 0.2);
  border-radius: 6px;
  transition: var(--transition);
}

.github-link:hover {
  background: rgba(100, 255, 218, 0.2);
  transform: translateY(-2px);
}

.mobile-menu {
  display: none;
  flex-direction: column;
  gap: 4px;
  cursor: pointer;
  padding: 8px;
}

.mobile-menu span {
  width: 25px;
  height: 2px;
  background: var(--text);
  transition: var(--transition);
  border-radius: 2px;
}

.mobile-menu.active span:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
}

.mobile-menu.active span:nth-child(2) {
  opacity: 0;
}

.mobile-menu.active span:nth-child(3) {
  transform: rotate(-45deg) translate(6px, -6px);
}

/* Hero Section */
.hero {
  padding: 180px 0 120px;
  position: relative;
  overflow: hidden;
  background: linear-gradient(135deg, var(--primary) 0%, var(--primary-light) 100%);
  display: flex;
  align-items: center;
  min-height: 100vh;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  overflow: hidden;
}

.background-logos-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.floating-logo-bg {
  position: absolute;
  opacity: 0.08;
  filter: brightness(0) invert(1);
  animation: floatLogo 20s ease-in-out infinite;
}

.floating-logo-bg img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.gradient-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    ellipse at center,
    rgba(10, 25, 47, 0.4) 0%,
    rgba(10, 25, 47, 0.85) 60%,
    rgba(10, 25, 47, 0.95) 100%
  );
  z-index: 1;
}

.hero-content-grid {
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: 1fr 1.5fr 1fr;
  gap: 40px;
  align-items: start;
}

.hero-column {
  background: rgba(17, 34, 64, 0.5);
  backdrop-filter: blur(10px);
  padding: 30px;
  border-radius: 16px;
  border: 1px solid rgba(100, 255, 218, 0.1);
  transition: var(--transition);
}

.hero-column:hover {
  background: rgba(17, 34, 64, 0.7);
  border-color: rgba(100, 255, 218, 0.3);
  transform: translateY(-5px);
}

.hero-column-center {
  background: transparent;
  backdrop-filter: none;
  border: none;
  text-align: center;
  padding: 0;
}

.hero-column-center:hover {
  transform: none;
}

.column-icon {
  width: 70px;
  height: 70px;
  background: var(--gradient);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary);
  margin-bottom: 20px;
  transition: var(--transition);
}

.hero-column:hover .column-icon {
  transform: scale(1.1) rotate(10deg);
}

.hero-column h3 {
  font-size: 1.4rem;
  color: var(--text);
  margin-bottom: 15px;
  font-weight: 700;
}

.hero-column p {
  color: var(--text-light);
  line-height: 1.6;
  margin-bottom: 20px;
}

.column-stats {
  display: flex;
  gap: 30px;
  margin-top: 25px;
}

.stat-item {
  display: flex;
  flex-direction: column;
}

.stat-number {
  font-size: 2rem;
  font-weight: 800;
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-light);
  margin-top: 5px;
}

.column-highlights {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 25px;
}

.highlight-item {
  display: flex;
  align-items: center;
  gap: 12px;
  color: var(--text-light);
  font-size: 0.95rem;
}

.highlight-dot {
  width: 8px;
  height: 8px;
  background: var(--secondary);
  border-radius: 50%;
  flex-shrink: 0;
  animation: pulse 2s ease-in-out infinite;
}

.hero-badge {
  display: inline-block;
  padding: 8px 16px;
  background: rgba(100, 255, 218, 0.1);
  border: 1px solid rgba(100, 255, 218, 0.2);
  border-radius: 20px;
  margin-bottom: 30px;
  animation: badgePulse 3s ease-in-out infinite;
}

.hero-badge span {
  color: var(--secondary);
  font-size: 0.9rem;
  font-weight: 600;
}

.hero-column-center h1 {
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 1.1;
  margin-bottom: 25px;
  color: var(--text);
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
}

.gradient-text {
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: gradientShift 4s ease-in-out infinite;
  background-size: 200% 200%;
}

.hero-main-text {
  font-size: 1.2rem;
  margin-bottom: 40px;
  color: var(--text-light);
  line-height: 1.6;
  max-width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.hero-actions {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.btn {
  display: inline-flex;
  align-items: center;
  padding: 14px 32px;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  text-decoration: none;
  transition: var(--transition);
  cursor: pointer;
  font-size: 1rem;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.2), transparent);
  transition: left 0.5s;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: var(--gradient);
  color: var(--primary);
  box-shadow: var(--shadow);
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-lg);
}

.btn-secondary {
  background: transparent;
  color: var(--text);
  border: 1px solid var(--text-light);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--secondary);
  transform: translateY(-3px);
}

/* Sections */
section {
  padding: 100px 0;
  position: relative;
  background: rgba(10, 25, 47, 0.7);
  backdrop-filter: blur(10px);
}

section:nth-child(even) {
  background: rgba(17, 34, 64, 0.7);
}

.section-header {
  text-align: center;
  margin-bottom: 60px;
}

.section-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text);
  margin-bottom: 20px;
  position: relative;
  display: inline-block;
}

.section-header h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: var(--gradient);
  border-radius: 2px;
  animation: lineExtend 2s ease-in-out infinite;
}

.section-header p {
  font-size: 1.1rem;
  color: var(--text-light);
  max-width: 600px;
  margin: 0 auto;
}

/* About Section */
.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  align-items: center;
}

.about-text p {
  margin-bottom: 20px;
  color: var(--text-light);
  line-height: 1.6;
}

.about-visual {
  display: flex;
  justify-content: center;
}

.visual-card {
  width: 300px;
  height: 300px;
  background: var(--primary);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  box-shadow: var(--shadow);
  border: 1px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
  transition: var(--transition);
}

.visual-card:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-lg);
  border-color: var(--secondary);
}

.visual-card img {
  width: 70%;
  height: 70%;
  object-fit: contain;
  transition: var(--transition);
}

.visual-card:hover img {
  transform: scale(1.1);
}

/* Researchers Section */
.researchers-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 30px;
}

.researcher-card {
  background: var(--primary-light);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--shadow);
  transition: var(--transition);
  border: 1px solid rgba(255, 255, 255, 0.05);
  position: relative;
}

.researcher-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--gradient);
  opacity: 0;
  transition: var(--transition);
  z-index: 1;
}

.researcher-card:hover::before {
  opacity: 0.05;
}

.researcher-card:hover {
  transform: translateY(-10px);
  box-shadow: var(--shadow-lg);
  border-color: rgba(100, 255, 218, 0.2);
}

.researcher-image {
  height: 300px;
  position: relative;
  overflow: hidden;
}

.researcher-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition);
}

.researcher-card:hover .researcher-image img {
  transform: scale(1.05);
}

.researcher-info {
  padding: 30px;
  position: relative;
  z-index: 2;
}

.researcher-info h3 {
  font-size: 1.4rem;
  color: var(--text);
  margin-bottom: 8px;
  font-weight: 600;
}

.researcher-info .title {
  color: var(--secondary);
  font-weight: 600;
  margin-bottom: 15px;
  font-size: 0.95rem;
}

/* Research Areas */
.areas-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
}

.area-card {
  background: var(--primary);
  padding: 40px 30px;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  transition: var(--transition);
  border: 1px solid rgba(255, 255, 255, 0.05);
  text-align: center;
  position: relative;
  overflow: hidden;
}

.area-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: var(--gradient);
  opacity: 0.1;
  transition: var(--transition);
}

.area-card:hover::before {
  left: 0;
}

.area-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
  border-color: rgba(100, 255, 218, 0.2);
}

.area-icon {
  width: 70px;
  height: 70px;
  margin: 0 auto 20px;
  background-color: var(--primary-light);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--secondary);
  font-size: 1.8rem;
  transition: var(--transition);
  position: relative;
  z-index: 2;
}

.area-card:hover .area-icon {
  background-color: var(--secondary);
  color: var(--primary);
  transform: rotateY(180deg);
}

.area-card h3 {
  margin-bottom: 15px;
  color: var(--text);
  font-weight: 600;
  position: relative;
  z-index: 2;
}

.area-card p {
  color: var(--text-light);
  line-height: 1.6;
  position: relative;
  z-index: 2;
}

/* Publications */
.publications-list {
  max-width: 800px;
  margin: 0 auto;
}

.publication-item {
  padding: 30px;
  border-left: 4px solid var(--secondary);
  background: var(--primary-light);
  margin-bottom: 20px;
  transition: var(--transition);
  border-radius: 0 10px 10px 0;
  position: relative;
  overflow: hidden;
}

.publication-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: var(--gradient);
  opacity: 0.05;
  transition: var(--transition);
}

.publication-item:hover::before {
  left: 0;
}

.publication-item:hover {
  transform: translateX(10px);
  box-shadow: var(--shadow);
}

.publication-item h3 {
  margin-bottom: 10px;
  color: var(--text);
  font-weight: 600;
  position: relative;
  z-index: 2;
}

.publication-meta {
  color: var(--secondary);
  font-size: 0.9rem;
  margin-bottom: 10px;
  font-weight: 500;
  position: relative;
  z-index: 2;
}

/* Footer */
footer {
  background: var(--primary);
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  padding: 60px 0 30px;
  position: relative;
}

.footer-content {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 50px;
}

.footer-about {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 10px;
}

.footer-links h4 {
  color: var(--text);
  font-size: 1.1rem;
  margin-bottom: 20px;
  font-weight: 600;
}

.footer-links ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.footer-links li {
  margin-bottom: 10px;
}

.footer-links a {
  color: var(--text-light);
  text-decoration: none;
  transition: var(--transition);
  font-size: 0.95rem;
}

.footer-links a:hover {
  color: var(--secondary);
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  margin-top: 40px;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.copyright {
  color: var(--text-light);
  font-size: 0.9rem;
}

/* Enhanced Animations */
@keyframes floatLogo {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
    opacity: 0.08;
  }
  25% {
    transform: translateY(-20px) rotate(5deg);
    opacity: 0.12;
  }
  50% {
    transform: translateY(-40px) rotate(0deg);
    opacity: 0.15;
  }
  75% {
    transform: translateY(-20px) rotate(-5deg);
    opacity: 0.12;
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.7;
  }
}

@keyframes badgePulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes gradientShift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

@keyframes lineExtend {
  0%, 100% {
    width: 80px;
  }
  50% {
    width: 120px;
  }
}

.fade-in-up,
.fade-in-left,
.fade-in-right {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.fade-in-left {
  transform: translateX(-30px);
}

.fade-in-right {
  transform: translateX(30px);
}

.fade-in-up.animate,
.fade-in-left.animate,
.fade-in-right.animate {
  opacity: 1;
  transform: translate(0, 0);
}

/* Responsive Design */
@media (max-width: 1024px) {
  .hero-content-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .hero-column-center {
    order: -1;
  }
  
  .hero h1 {
    font-size: 2.8rem;
  }
  
  .about-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .footer-content {
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }
}

@media (max-width: 768px) {
  .nav-links {
    display: none;
    position: absolute;
    top: 80px;
    left: 0;
    width: 100%;
    background: var(--primary-light);
    flex-direction: column;
    padding: 20px;
    box-shadow: var(--shadow);
    border-top: 1px solid rgba(255, 255, 255, 0.05);
  }
  
  .nav-links.active {
    display: flex;
  }
  
  .mobile-menu {
    display: flex;
  }
  
  .hero {
    padding: 140px 0 80px;
    min-height: 80vh;
  }
  
  .hero h1 {
    font-size: 2.2rem;
  }
  
  .hero-main-text {
    font-size: 1.1rem;
  }
  
  .column-stats {
    justify-content: center;
  }
  
  .researchers-grid,
  .areas-grid {
    grid-template-columns: 1fr;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .footer-bottom {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
}

@media (max-width: 576px) {
  .lab-name {
    font-size: 1rem;
  }
  
  .university-name {
    font-size: 0.75rem;
  }
  
  .hero h1 {
    font-size: 1.8rem;
  }
  
  .hero-main-text {
    font-size: 1rem;
  }
  
  .section-header h2 {
    font-size: 2rem;
  }
  
  .btn {
    padding: 12px 24px;
    width: 100%;
    justify-content: center;
  }
  
  .hero-actions {
    flex-direction: column;
    width: 100%;
  }
  
  .hero-column {
    padding: 20px;
  }
  
  .column-icon {
    width: 60px;
    height: 60px;
  }
  
  .hero-column h3 {
    font-size: 1.2rem;
  }
}